.signUpPage{
    display: flex;
    margin:5% auto;
    justify-content: space-around;
    align-items: center;
    
    width: 80%;
    padding: 1%;

    background-color: #F4E7E1;
    border: 3px solid #FF9B45;
    border-radius: 25px;


}

.imgSection{
    width: 20%;
}

.formSection{
    width: 40%;
}

.pageHead{
    color: #E67514;
}

.formInput{
    width: 80%;
    font-size: medium;
    border-radius: 25px;
    padding:1%;
}

.formInputLabel{
    color: #090040;
}

.signUpBtn{
    margin: 4% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
    width: 80%;
}

/* MEDIA QUERIES */

/* Tablet (â‰¤768px) */
@media screen and (max-width: 768px) {
  .signUpPage {
    flex-direction: column;
    gap: 2rem;
    margin: 5% 2%;
  }

  .imgSection {
    width: 40%;
  }

  .formSection {
    width: 90%;
    text-align: center;
  }

  .formInput {
    width: 80%;
    font-size: medium;
  }

  .signUpBtn {
    width: 60%;
    font-size: medium;
  }
}

/* Mobile (â‰¤480px) */
@media screen and (max-width: 480px) {
  .signUpPage {
    flex-direction: column;
    gap: 1.5rem;
    margin: 5% 2%;
  }

  .imgSection {
    width: 60%;
  }

  .formSection {
    width: 100%;
    text-align: center;
  }

  .formInputLabel {
    font-size: small;
  }

  .formInput {
    width: 90%;
    font-size: small;
  }

  .signUpBtn {
    width: 70%;
    padding: 3%;
    font-size: small;
  }

  .pageHead {
    font-size: x-large;
  }
}
