.productPage {
  padding: 2rem;
  background: #f9fafb;
  min-height: 100vh;
  text-align: center;
  margin: 5% auto;
  width: 70%;

}

.feedbackButton, .backButton {
  background-color: #4a90e2;
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 6px;
  cursor: pointer;
  margin: 0.5rem;
  font-weight: 500;
}

.feedbackButton:hover, .backButton:hover {
  background-color: #357abd;
}

.productGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
  justify-content: center;
}

.productCard {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  text-align: left;
  transition: transform 0.2s ease;
}

.productCard:hover {
  transform: translateY(-5px);
}
