.loginPage{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin:5% auto;

    width: 50%;
    padding: 1%;

    background-color: #F4E7E1;
    border: 3px solid #FF9B45;
    border-radius: 25px;
}

.pageHead{
    color: #E67514;
}

.loginIcon{
    width: 20%;
}

.loginDetails{
    width: 60%;
}

.loginInput{
    width: 70%;
    font-size: medium;
    border-radius: 25px;
    padding: 1%;
}

.formInputLabel{
    color: #090040;
}

.loginBtn{
    margin: 2% 0%;
    border-radius: 25px;
    background-color: black;
    border-color: #cccc;
    color: gold;
    font-size: medium;
    padding: 2%;
    cursor: pointer;
    width: 80%;
}


/* MEDIA QUERIES */

/* Tablet (â‰¤768px) */
@media screen and (max-width: 768px) {
  .loginPage {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin: 5% auto;
  }

  .loginIcon {
    width: 40%;
  }

  .loginDetails {
    width: 90%;
    text-align: center;
  }

  .loginInput {
    width: 80%;
    font-size: medium;
  }

  .loginBtn {
    width: 60%;
    font-size: medium;
  }
}

/* Mobile (â‰¤480px) */
@media screen and (max-width: 480px) {
  .loginPage {
    flex-direction: column;
    margin: 5% auto;
    gap: 1.5rem;
  }

  .loginIcon {
    width: 50%;
  }

  .loginDetails {
    width: 100%;
    text-align: center;
  }

  .loginInput {
    width: 90%;
    font-size: small;
  }

  .loginBtn {
    width: 70%;
    padding: 3%;
    font-size: small;
  }

  .formInputLabel {
    font-size: small;
  }

  .pageHead {
    font-size: x-large;
  }
}
