.dashboard {
  padding: 2rem;
}

.infoBox {
  background: #f7f7f7;
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 2rem;
}

.section {
  margin-top: 2rem;
  background: #fff;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.addForm input {
  margin-right: 10px;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.addForm button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 6px;
  cursor: pointer;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th, .table td {
  border: 1px solid #ddd;
  padding: 10px;
}

.stockInput {
  width: 60px;
  padding: 5px;
  text-align: center;
}

.suggestionList {
  list-style-type: disc;
  margin-left: 1.5rem;
}

.charts {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.chartBox {
  background: #fff;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}


.noSupplier {
  text-align: center;
  padding: 100px 20px;
}

.addBusinessBtn {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 14px 28px;
  border-radius: 10px;
  font-size: 18px;
  margin-top: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.addBusinessBtn:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

.loading {
  text-align: center;
  font-size: 18px;
  padding: 50px;
  color: #666;
}



.modalOverlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
}

.modalContent {
  background: white;
  padding: 30px;
  border-radius: 12px;
  width: 400px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.modalContent h3 {
  text-align: center;
  margin-bottom: 20px;
}

.modalForm input,
.modalForm textarea {
  width: 100%;
  margin-bottom: 12px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.modalForm textarea {
  height: 70px;
  resize: none;
}

.modalButtons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.modalButtons button {
  flex: 1;
  margin: 0 5px;
  padding: 10px;
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.modalButtons button:first-child {
  background-color: #4caf50;
}

.cancelBtn {
  background-color: #f44336;
}

.cancelBtn:hover {
  background-color: #e53935;
}

.suggestionBox {
  background: #f9f9ff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  line-height: 1.6;
  color: #333;
  font-size: 0.95rem;
  white-space: pre-wrap;
  margin-top: 0.5rem;
}
